import React, { useRef } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import { EffectCoverflow, FreeMode, Scrollbar, Mousewheel } from 'swiper/modules';

function Swipers({ children, className, direction = "horizontal" }) {
  const swiperRef = useRef(null);

  // ÿßÿ≥ÿ™ÿß€åŸÑ‚ÄåŸáÿß€å ÿ≥Ÿá ÿ®ÿπÿØ€å
  const swiperStyle = {
    '--swiper-perspective': '1000px', // ÿ™ŸÜÿ∏€åŸÖ Ÿæÿ±ÿ≥Ÿæ⁄©ÿ™€åŸà
    '--swiper-3d-rotate': '10deg', // ÿ™ŸÜÿ∏€åŸÖ ÿ≤ÿßŸà€åŸá ⁄Üÿ±ÿÆÿ¥
  };

  return (
    <Swiper
      ref={swiperRef}
      direction={direction}
      effect={'coverflow'} // ÿßÿ≥ÿ™ŸÅÿßÿØŸá ÿßÿ≤ ÿßŸÅ⁄©ÿ™ coverflow ÿ®ÿ±ÿß€å ÿ¨ŸÑŸàŸá ÿ≥Ÿá ÿ®ÿπÿØ€å
      coverflowEffect={{
        rotate: 20, // ⁄Üÿ±ÿÆÿ¥ ÿßÿ≥ŸÑÿß€åÿØŸáÿß
        stretch: 5, // ⁄©ÿ¥€åÿØ⁄Ø€å
        depth: 100, // ÿπŸÖŸÇ ÿ≥Ÿá ÿ®ÿπÿØ€å
        modifier: 1, // ÿ™ÿπÿØ€åŸÑ ⁄©ŸÜŸÜÿØŸá
        slideShadows: false, // ŸÜŸÖÿß€åÿ¥ ÿ≥ÿß€åŸá
      }}
      slidesPerView={'auto'}
      freeMode={true}
      scrollbar={{
        hide: true,
        draggable: true,
      }}
      mousewheel={{
        forceToAxis: true,
        sensitivity: 100,
      }}
      modules={[EffectCoverflow, FreeMode, Scrollbar, Mousewheel]}
      className={`mySwiper ${className || ''}`}
      style={swiperStyle}
      spaceBetween={direction === 'horizontal' ? 40 : 16} // ÿßŸÅÿ≤ÿß€åÿ¥ ŸÅÿßÿµŸÑŸá ÿ®ÿ±ÿß€å ÿ¨ŸÑŸàŸá ÿ®Ÿáÿ™ÿ±
      grabCursor={true} // ÿ™ÿ∫€å€åÿ± ÿ¥⁄©ŸÑ ⁄©ÿ±ÿ≥ÿ± ŸáŸÜ⁄ØÿßŸÖ hover
    >
      {React.Children.map(children, (child, index) => (
        <SwiperSlide 
          key={index} 
          style={{ 
            width: direction === 'horizontal' ? 'auto' : '100%',
            height: direction === 'vertical' ? 'auto' : '100%',
            transformStyle: 'preserve-3d', // ŸÅÿπÿßŸÑ ⁄©ÿ±ÿØŸÜ transformŸáÿß€å ÿ≥Ÿá ÿ®ÿπÿØ€å
            backfaceVisibility: 'hidden', // ŸÖÿÆŸÅ€å ⁄©ÿ±ÿØŸÜ Ÿæÿ¥ÿ™ ÿßÿ≥ŸÑÿß€åÿØŸáÿß
          }}
        >
          <div style={{
            transform: 'translateZ(10px)', // ÿß€åÿ¨ÿßÿØ ÿπŸÖŸÇ
            height: '100%',
          }}>
            {child}
          </div>
        </SwiperSlide>
      ))}
    </Swiper>
  );
}

export default React.memo(Swipers);